* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  box-sizing: border-box;
  margin: 0;
  text-align: center;
  color: white;
  background-color: black;
  background-image: url(/static/media/backgroundDark.14111261.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}


body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: inline-block;
}

button, a {
    border-radius: 12px;
    border-color: none;
}

header {
    padding: 30px 0;
    color: purple;
    text-align: center;
    width: 100%;
}

input, form {
    background-color: #3CBC8D;
}

#headerText {
    font-family: 'Courier New', Courier, monospace;
}

#header_image {
    width: 100%;
}
.search-bar{
    margin: 40px 0 20px;
    font-size: x-large;
}

#searchBar {
    background: black;
    color: white;
}
.cards {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }

.card {
    margin: 20px;
}

.card-img-top {
    min-height: 450px;
}

.card-body{
    /* border: #ffea02 1px solid; */
}

.card-title {
    min-height: 72px;
    margin-top: .75rem;
}
.desc{
    overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 7;
  -webkit-box-orient: vertical;
  font-weight: 700;
}

a {
    color: red;
    text-decoration: none;
    font-weight: 700;
}

a:hover{
    color: #ffea02;
}

.list-group {
    font-weight: 900;
    /* border: #ffea02 solid 1px; */
}

.btn-info {
    color: #ffea02;
    border-color: red;
}

.list-group-item {
    /* border: 1px solid #ffea02; */
}
.container{
    background-image: url(/static/media/background.26e9e6f9.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-color: black;
}
